import { useState, useEffect } from 'react';
import { useMutation } from '@tanstack/react-query';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import { verify2FAMock } from '../lib/apiMocks';
import TwoFAButton from '../shared/ui/TwoFAButton';
import { useTwoFATimer } from '../hooks/useTwoFATimer';
import { useTwoFACode } from '../hooks/useTwoFACode';
import {
  shouldShowButton,
  getButtonType,
  isButtonDisabled,
  getBorderColor,
  getBorderWidth,
} from '../lib/twoFAHelpers';

interface Props {
  setError: (error: string | null) => void;
}

type TwoFAState = 'initial' | 'expired' | 'invalid' | 'filled';

export default function TwoFAForm({ setError }: Props) {
  const [state, setState] = useState<TwoFAState>('initial');

  const { startTimer, clearTimer } = useTwoFATimer(setState, setError, state);
  const { code, inputs, handleChange, handleKeyDown, resetCode } = useTwoFACode(
    setState,
    setError,
    state
  );

  const mutation = useMutation({
    mutationFn: verify2FAMock,
    onSuccess: () => {
      clearTimer();
      alert('2FA verified!');
    },
    onError: (error: any) => {
      if (
        error.message.includes('expired') ||
        error.message.includes('Expired')
      ) {
        setState('expired');
        setError(null);
      } else {
        setState('invalid');
        setError(null);
      }
    },
  });

  const handleSubmit = () => {
    const fullCode = code.join('');
    mutation.mutate({ code: fullCode });
  };

  const handleGetNew = () => {
    resetCode();
    setState('initial');
    setError(null);
    startTimer();
    alert('New code sent!');
    inputs.current[0]?.focus();
  };

  useEffect(() => {
    inputs.current[0]?.focus();
  }, []);

  const showButton = shouldShowButton(state);
  const buttonType = getButtonType(state);
  const buttonDisabled = isButtonDisabled(state);

  return (
    <>
      <Typography
        variant="h5"
        align="center"
        gutterBottom
        sx={{
          lineHeight: '32px',
          fontSize: '24px',
          fontWeight: 600,
          mb: '8px',
          color: 'rgba(0, 0, 0, 0.88)',
        }}
      >
        Two-Factor Authentication
      </Typography>
      <Typography
        variant="body1"
        align="center"
        sx={{
          fontSize: '16px',
          color: 'rgba(0, 0, 0, 0.88)',
          mb: 3,
          lineHeight: '24px',
        }}
      >
        Enter the 6-digit code from the Google Authenticator app
      </Typography>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'space-between',
          gap: '8px',
          mb: 1,
        }}
      >
        {code.map((digit, index) => (
          <TextField
            key={index}
            inputRef={(el) => (inputs.current[index] = el)}
            value={digit}
            onChange={(e) => handleChange(index, e.target.value)}
            onKeyDown={(e) => handleKeyDown(index, e)}
            disabled={state === 'expired'}
            inputProps={{
              maxLength: 1,
              style: { textAlign: 'center', fontSize: '20px', fontWeight: 500 },
            }}
            variant="outlined"
            sx={{
              width: '52.67px',
              '& .MuiOutlinedInput-root': {
                height: '60px',
                '& fieldset': {
                  borderColor: getBorderColor(state),
                  borderWidth: getBorderWidth(state),
                },
                '&:hover fieldset': {
                  borderColor:
                    state !== 'initial' && state !== 'filled'
                      ? getBorderColor(state)
                      : undefined,
                  borderWidth: getBorderWidth(state),
                },
                '&.Mui-focused fieldset': {
                  borderColor:
                    state !== 'initial' && state !== 'filled'
                      ? getBorderColor(state)
                      : undefined,
                  borderWidth: getBorderWidth(state),
                },
              },
            }}
          />
        ))}
      </Box>
      {state === 'invalid' && (
        <Typography
          sx={{
            color: '#ff4d4f',
            fontSize: '16px',
            mb: 2,
          }}
        >
          Invalid code
        </Typography>
      )}
      {showButton && (
        <Box sx={{ mt: 3 }}>
          <TwoFAButton
            type={buttonType}
            disabled={buttonDisabled}
            loading={mutation.isPending}
            onClick={buttonType === 'getNew' ? handleGetNew : handleSubmit}
          />
        </Box>
      )}
    </>
  );
}
