import { useState } from 'react';
import { useMutation } from '@tanstack/react-query';
import { loginMock } from '../lib/apiMocks';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import InputAdornment from '@mui/material/InputAdornment';
import { Box } from '@mui/material';
import Header from '../widgets/ui/Header';

interface Props {
  onSuccess: () => void;
  setError: (error: string | null) => void;
}

// Регулярное выражение для валидации email
const EMAIL_REGEX = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;

export default function AuthForm({ onSuccess, setError }: Props) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [emailError, setEmailError] = useState('');
  const [touched, setTouched] = useState(false);

  const validateEmail = (value: string) => {
    if (!value) {
      return 'Email is required';
    }
    if (!EMAIL_REGEX.test(value)) {
      return 'Please enter a valid email address';
    }
    return '';
  };

  const handleEmailChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setEmail(value);

    if (touched) {
      setEmailError(validateEmail(value));
    }
  };

  const handleEmailBlur = () => {
    setTouched(true);
    setEmailError(validateEmail(email));
  };

  const isDisabled = !email || !password || !!emailError;

  const mutation = useMutation({
    mutationFn: loginMock,
    onSuccess: (data) => {
      if (data.requires2FA) {
        onSuccess();
      } else {
        alert('Logged in!');
      }
    },
    onError: (error: any) => {
      setError(error.message);
    },
  });

  const handleSubmit = () => {
    // Проверяем email перед отправкой
    const emailValidationError = validateEmail(email);
    if (emailValidationError) {
      setTouched(true);
      setEmailError(emailValidationError);
      return;
    }

    mutation.mutate({ email, password });
  };

  return (
    <Box
      sx={{
        p: 2,
        boxShadow: 3,
        borderRadius: '6px',
        minHeight: '372px',
        width: '440px',
        padding: '32px',
      }}
    >
      <Header />
      <Typography
        variant="h5"
        align="center"
        gutterBottom
        sx={{
          lineHeight: '32px',
          fontSize: '24px',
          fontWeight: 600,
          mb: '24px',
        }}
      >
        Sign in to your account to continue
      </Typography>

      <Box sx={{ mb: 2 }}>
        <TextField
          fullWidth
          placeholder="Email"
          variant="outlined"
          value={email}
          onChange={handleEmailChange}
          onBlur={handleEmailBlur}
          error={touched && !!emailError}
          sx={{
            '& .MuiOutlinedInput-root': {
              height: '40px',
              padding: '8px 11px',
              '& fieldset': {
                borderColor: (theme) =>
                  touched && emailError
                    ? theme.palette.error.main
                    : theme.palette.primary.light,
              },
              '&:hover': {
                '& .MuiOutlinedInput-notchedOutline': {
                  borderColor: (theme) =>
                    touched && emailError
                      ? theme.palette.error.main
                      : theme.palette.primary.main,
                  boxShadow:
                    touched && emailError
                      ? '0px 0px 0px 2px rgba(255, 77, 79, 0.1)'
                      : '0px 0px 0px 2px rgba(5, 145, 255, 0.1)',
                },
              },
              '&.Mui-focused': {
                '& .MuiOutlinedInput-notchedOutline': {
                  borderColor: (theme) =>
                    touched && emailError
                      ? theme.palette.error.main
                      : theme.palette.primary.main,
                },
              },
            },
          }}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <img
                  src="/images/login.svg"
                  alt="Login icon"
                  style={{ width: 16, height: 16 }}
                />
              </InputAdornment>
            ),
          }}
          autoFocus
        />
        {touched && emailError && (
          <Typography
            sx={{
              color: (theme) => theme.palette.error.main,
              fontSize: '14px',
              mt: '4px',
              lineHeight: '20px',
            }}
          >
            {emailError}
          </Typography>
        )}
      </Box>

      <TextField
        fullWidth
        placeholder="Password"
        type="password"
        variant="outlined"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
        sx={{
          mb: 2,
          '& .MuiOutlinedInput-root': {
            height: '40px',
            padding: '8px 11px',
            '&:hover': {
              '& .MuiOutlinedInput-notchedOutline': {
                borderColor: (theme) => theme.palette.primary.main,
                boxShadow: '0px 0px 0px 2px rgba(5, 145, 255, 0.1)',
              },
            },
          },
        }}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <img
                src="/images/lock.svg"
                alt="Lock icon"
                style={{ width: 16, height: 16 }}
              />
            </InputAdornment>
          ),
        }}
      />

      <Button
        fullWidth
        variant="contained"
        color="primary"
        onClick={handleSubmit}
        disabled={isDisabled || mutation.isPending}
        sx={{
          backgroundColor: (theme) =>
            isDisabled || mutation.isPending
              ? theme.palette.primary.dark
              : theme.palette.primary.main,
          color: '#fff',
          height: '40px',
          '&:hover': {
            backgroundColor: (theme) =>
              isDisabled || mutation.isPending
                ? theme.palette.primary.dark
                : theme.palette.primary.main,
          },
          '&.Mui-disabled': {
            backgroundColor: (theme) => theme.palette.primary.dark,
            color: '#fff',
            opacity: 0.6,
          },
        }}
      >
        {mutation.isPending ? 'Logging in...' : 'Log in'}
      </Button>
    </Box>
  );
}
